@import "../../vars.scss";

.thingsToDo {
    margin: 50px auto;
    max-width: 800px;

    h1 {
        font-size: 50px;
        margin: 0 0 60px;
    }

    h2 {
        font-size: 36px;
        margin: 20px;
        letter-spacing: 4px;
    }

    p {
        font-family: $font-secondary;
        font-size: 18px;
    }

    li {
        list-style: none;
        font-family: $font-secondary;
        font-size: 18px;
        margin-bottom: 20px;

        h3 {
            text-transform: uppercase;
        }
    }
}